
.home{
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}
.headerContainer{
    width: auto;
    margin-left: 30px;
}
.home .headerContainer h1{
    justify-content: cneter;
    align-items: center;
    font-weight: bold;
    font-size: 60px;
     font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     color: black;
}
.home .headerContainer p{
    margin-top: 15px;
    padding: 0;
    font-size: 30px;
    font-weight: lighter;
    color: black;
    
}
.home .headerContainer button{
    color: white;
    margin-top: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    height: 40px;
    width: 150px;
    text-decoration: none;
    background-color: black;
    font-size: 20px;
    font-weight: bold;
}
.home .headerContainer button:hover{
    background-color: aqua;
    cursor: pointer;
    color: black;
    transition: 0.3s;
}
@media only screen and ( max-width: 600px){
    .home{
        justify-content: center;
        align-items: center;

    }
    .headerContainer{
        margin-left: 0;
        border-radius: 10px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
         margin-top: 20px;
        color: rgb(197, 243, 191);
        background-color: #a8cfcf;
    }
    .home .headerContainer h1{
       font-size: 40px;
        height: 30px;
        margin: 30px 0px;
       color: black
    }
    .home .headerContainer p {
         color: black;
         font-size: 40px;
    }
    .headerContainer button{
         background-color: rgb(15, 85, 236);
         color: #121619;
         margin-bottom: 30px;
    }
}